
import { defineComponent } from "vue";
import RadarChart from "./RadarChart.vue";

export default defineComponent({
  name: "ModalChart",
  props: ["open", "skills", "data", "labels"],
  emits: ["skills", "close"],
  components: {
    RadarChart,
  },
  methods: {
    addSkill(index: number) {
      this.data[index]++;
      this.$emit("skills", this.data);
    },
    close() {
      this.$emit("close");
    },
  },
});
